'use client';

import { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import UsernameInput from '@/components/UsernameInput';
import { createAnonymousUser } from '@/lib/utils';
import { User } from '@/types';

export default function Home() {
  const [displayText, setDisplayText] = useState('');
  const [showCursor, setShowCursor] = useState(true);
  const [showUsernameInput, setShowUsernameInput] = useState(false);
  const [currentUser, setCurrentUser] = useState<User | null>(null);
  const fullText = "Conversations that vanish like shadows.";

  useEffect(() => {
    let i = 0;
    const timer = setInterval(() => {
      if (i < fullText.length) {
        setDisplayText(fullText.slice(0, i + 1));
        i++;
      } else {
        clearInterval(timer);
      }
    }, 100);

    return () => clearInterval(timer);
  }, []);

  useEffect(() => {
    const cursorTimer = setInterval(() => {
      setShowCursor(prev => !prev);
    }, 500);

    return () => clearInterval(cursorTimer);
  }, []);

  const handleStartChatting = () => {
    setShowUsernameInput(true);
  };

  const handleUsernameSubmit = (username: string) => {
    const user = createAnonymousUser(username);
    setCurrentUser(user);
    // TODO: Navigate to chat interface
    console.log('User created:', user);
  };

  const handleBackToHome = () => {
    setShowUsernameInput(false);
  };

  // Show username input if user clicked "Start Chatting"
  if (showUsernameInput) {
    return (
      <UsernameInput
        onSubmit={handleUsernameSubmit}
        onBack={handleBackToHome}
      />
    );
  }

  return (
    <div className="min-h-screen flex flex-col items-center justify-center px-4 relative overflow-hidden">
      {/* Background Effects */}
      <div className="absolute inset-0 bg-gradient-to-br from-nox-black via-nox-dark to-nox-darker"></div>
      <div className="absolute inset-0 bg-[radial-gradient(circle_at_50%_50%,rgba(0,255,255,0.1),transparent_50%)]"></div>

      {/* Main Content */}
      <motion.div
        className="relative z-10 text-center max-w-4xl mx-auto"
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.8 }}
      >
        {/* NOX Logo */}
        <motion.h1
          className="text-8xl md:text-9xl font-bold mb-8 bg-gradient-to-r from-neon-cyan via-neon-purple to-neon-pink bg-clip-text text-transparent"
          initial={{ scale: 0.5, opacity: 0 }}
          animate={{ scale: 1, opacity: 1 }}
          transition={{ duration: 1, delay: 0.2 }}
        >
          NOX
        </motion.h1>

        {/* Typewriter Tagline */}
        <div className="mb-12 h-16 flex items-center justify-center">
          <p className="text-xl md:text-2xl text-nox-text font-mono">
            {displayText}
            <span className={`${showCursor ? 'opacity-100' : 'opacity-0'} text-neon-cyan`}>|</span>
          </p>
        </div>

        {/* CTA Button */}
        <motion.button
          onClick={handleStartChatting}
          className="glass rounded-2xl px-8 py-4 text-lg font-semibold text-nox-text border border-neon-cyan/30 hover:border-neon-cyan/60 transition-all duration-300 hover:shadow-neon group relative overflow-hidden"
          whileHover={{ scale: 1.05 }}
          whileTap={{ scale: 0.95 }}
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8, delay: 1 }}
        >
          <span className="relative z-10">Start Chatting</span>
          <div className="absolute inset-0 bg-gradient-to-r from-neon-cyan/20 to-neon-purple/20 opacity-0 group-hover:opacity-100 transition-opacity duration-300"></div>
        </motion.button>

        {/* Features */}
        <motion.div
          className="mt-16 grid grid-cols-1 md:grid-cols-3 gap-6 max-w-3xl mx-auto"
          initial={{ opacity: 0, y: 40 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8, delay: 1.5 }}
        >
          <div className="glass rounded-xl p-6 border border-white/10 hover:border-neon-cyan/30 transition-all duration-300">
            <div className="text-neon-cyan text-2xl mb-3">👤</div>
            <h3 className="text-lg font-semibold mb-2 text-nox-text">Anonymous</h3>
            <p className="text-nox-text-dim text-sm">No accounts, no personal data. Just pure communication.</p>
          </div>

          <div className="glass rounded-xl p-6 border border-white/10 hover:border-neon-purple/30 transition-all duration-300">
            <div className="text-neon-purple text-2xl mb-3">⚡</div>
            <h3 className="text-lg font-semibold mb-2 text-nox-text">Ephemeral</h3>
            <p className="text-nox-text-dim text-sm">Messages vanish after being read and replied to.</p>
          </div>

          <div className="glass rounded-xl p-6 border border-white/10 hover:border-neon-pink/30 transition-all duration-300">
            <div className="text-neon-pink text-2xl mb-3">🔒</div>
            <h3 className="text-lg font-semibold mb-2 text-nox-text">Secure</h3>
            <p className="text-nox-text-dim text-sm">Real-time encrypted communication with zero trace.</p>
          </div>
        </motion.div>
      </motion.div>

      {/* Footer */}
      <motion.footer
        className="absolute bottom-8 text-center text-nox-text-dim text-sm"
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ duration: 0.8, delay: 2 }}
      >
        <p>Chat like a shadow. No history. No trace.</p>
      </motion.footer>
    </div>
  );
}
