'use client';

import { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import { generateUsername, isValidUsername } from '@/lib/utils';

interface UsernameInputProps {
  onSubmit: (username: string) => void;
  onBack: () => void;
}

export default function UsernameInput({ onSubmit, onBack }: UsernameInputProps) {
  const [username, setUsername] = useState('');
  const [isValid, setIsValid] = useState(false);
  const [showError, setShowError] = useState(false);
  const [suggestedUsername, setSuggestedUsername] = useState('');

  useEffect(() => {
    // Generate a suggested username on component mount
    setSuggestedUsername(generateUsername());
  }, []);

  useEffect(() => {
    const valid = isValidUsername(username);
    setIsValid(valid);
    if (username.length > 0 && !valid) {
      setShowError(true);
    } else {
      setShowError(false);
    }
  }, [username]);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (isValid) {
      onSubmit(username);
    }
  };

  const useSuggested = () => {
    setUsername(suggestedUsername);
  };

  const generateNew = () => {
    const newSuggestion = generateUsername();
    setSuggestedUsername(newSuggestion);
  };

  return (
    <div className="min-h-screen flex flex-col items-center justify-center px-4 relative overflow-hidden">
      {/* Background Effects */}
      <div className="absolute inset-0 bg-gradient-to-br from-nox-black via-nox-dark to-nox-darker"></div>
      <div className="absolute inset-0 bg-[radial-gradient(circle_at_50%_50%,rgba(139,92,246,0.1),transparent_50%)]"></div>
      
      {/* Back Button */}
      <motion.button
        onClick={onBack}
        className="absolute top-8 left-8 glass rounded-xl px-4 py-2 text-nox-text hover:text-neon-cyan transition-colors duration-300 border border-white/10 hover:border-neon-cyan/30"
        whileHover={{ scale: 1.05 }}
        whileTap={{ scale: 0.95 }}
      >
        ← Back
      </motion.button>

      {/* Main Content */}
      <motion.div 
        className="relative z-10 text-center max-w-md mx-auto w-full"
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.8 }}
      >
        {/* Title */}
        <motion.h1 
          className="text-4xl md:text-5xl font-bold mb-4 bg-gradient-to-r from-neon-purple via-neon-pink to-neon-cyan bg-clip-text text-transparent"
          initial={{ scale: 0.5, opacity: 0 }}
          animate={{ scale: 1, opacity: 1 }}
          transition={{ duration: 1, delay: 0.2 }}
        >
          Choose Your Shadow
        </motion.h1>

        <motion.p 
          className="text-nox-text-dim mb-8 text-lg"
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ duration: 0.8, delay: 0.4 }}
        >
          Enter a username to begin your anonymous journey
        </motion.p>

        {/* Username Form */}
        <motion.form 
          onSubmit={handleSubmit}
          className="space-y-6"
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8, delay: 0.6 }}
        >
          <div className="relative">
            <input
              type="text"
              value={username}
              onChange={(e) => setUsername(e.target.value)}
              placeholder="Enter username..."
              className="w-full glass rounded-xl px-6 py-4 text-lg text-nox-text placeholder-nox-text-dim border border-white/20 focus:border-neon-purple/60 focus:outline-none focus:shadow-neon-purple transition-all duration-300 bg-transparent"
              maxLength={20}
              autoFocus
            />
            {showError && (
              <motion.p 
                className="absolute -bottom-6 left-0 text-red-400 text-sm"
                initial={{ opacity: 0, y: -10 }}
                animate={{ opacity: 1, y: 0 }}
              >
                Username must be 3-20 characters, letters, numbers, _ or - only
              </motion.p>
            )}
          </div>

          {/* Suggested Username */}
          <div className="space-y-3">
            <p className="text-nox-text-dim text-sm">Or use a suggested username:</p>
            <div className="flex items-center gap-3">
              <button
                type="button"
                onClick={useSuggested}
                className="flex-1 glass rounded-xl px-4 py-3 text-neon-cyan border border-neon-cyan/30 hover:border-neon-cyan/60 hover:bg-neon-cyan/10 transition-all duration-300"
              >
                {suggestedUsername}
              </button>
              <button
                type="button"
                onClick={generateNew}
                className="glass rounded-xl px-4 py-3 text-nox-text hover:text-neon-purple border border-white/20 hover:border-neon-purple/30 transition-all duration-300"
                title="Generate new suggestion"
              >
                🎲
              </button>
            </div>
          </div>

          {/* Submit Button */}
          <motion.button
            type="submit"
            disabled={!isValid}
            className={`w-full rounded-xl px-6 py-4 text-lg font-semibold transition-all duration-300 ${
              isValid
                ? 'glass border border-neon-cyan/30 hover:border-neon-cyan/60 text-nox-text hover:shadow-neon hover:bg-neon-cyan/10'
                : 'bg-nox-gray border border-nox-light-gray text-nox-text-dim cursor-not-allowed'
            }`}
            whileHover={isValid ? { scale: 1.02 } : {}}
            whileTap={isValid ? { scale: 0.98 } : {}}
          >
            {isValid ? 'Enter the Shadows' : 'Enter Username'}
          </motion.button>
        </motion.form>

        {/* Info */}
        <motion.div 
          className="mt-8 text-center text-nox-text-dim text-sm space-y-2"
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ duration: 0.8, delay: 1 }}
        >
          <p>• No registration required</p>
          <p>• Username is temporary and anonymous</p>
          <p>• All data vanishes when you leave</p>
        </motion.div>
      </motion.div>
    </div>
  );
}
